import styles from "./contactme.module.scss";
import { useRef,useState } from "react";
import emailjs from "emailjs-com"

export default function Contactme() {
const form = useRef();

  let [state, upstate] = useState({
    name:"",
    contact:"",
    email:"",
    message:""
  })
  

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_dlemrgn', 'template_h2kk799', e.target, 'GNJXnY2oFvvzixi2N')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
      e.target.reset();
  };
  return (
    <div className={styles.contactme_Container}>
      <form ref={form} id="contact" target="#" action="POST" className="contactme" onSubmit={sendEmail}>
        <h1>
         
          <div  className={styles.get}>
          <span>G</span>
          <span>E</span>
          <span>T</span> 
          </div>
          <div className={styles.in}>
          <span>I</span>
          <span>N</span> 
          </div>
          <div className={styles.touch}>
          <span>T</span>
          <span>O</span>
          <span>U</span>
          <span>C</span>
          <span>H</span>
          </div>
        
        </h1>
        <div className={styles.nameandcontact}>
          <input type="text" required name="name" value ={state.name} onChange={event=>upstate({name:event.target.value})} placeholder="Your Name" />
          <input type="text" required name="contact" value ={state.contact} onChange={event=>upstate({contact:event.target.value})} placeholder=" Your Contact Number" />
        </div>
        <input type="text" name="email" required value ={state.email} placeholder=" Your email Address" onChange={event=>upstate({email:event.target.value})} />
        <textarea
        value ={state.message}
        onChange={event=>upstate({message:event.target.value})}
          type="text"
          name="message"
          id="message"
          cols="30"
          rows="10"
          placeholder=" Enter your message !"
          required
          reset
        ></textarea>
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <button type="submit">
            {" "}
            <span>Submit</span>
          </button>
        </div>
      </form>
    </div>
  );
}
